import { ref } from '@vue/composition-api'

// Notification
import { useToast } from 'vue-toastification/composition'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import axios from 'axios'

export default function useInvoicesList() {
  // Use toast
  const toast = useToast()

  const userData = JSON.parse(localStorage.getItem('userData'))

  const refInvoiceListTable = ref(null)

  // Table Handlers
  const tableColumns = [
    { key: 'id', label: '#id', sortable: false },
    { key: 'operation', label: 'Operao', sortable: false },
    { key: 'title', label: 'Ttulo', sortable: false },
    { key: 'desc', label: 'Descrio', sortable: false },
    { key: 'mealSize', label: 'Quantidade de Alimento', sortable: false },
    { key: 'creationDate', label: 'Criado em', sortable: false },
    { key: 'frequency.time', label: 'Frequncia', sortable: false },
    { key: 'actions', label: 'Aes' },
  ]
  const sortBy = ref('id')

  const fetchInvoices = (ctx, callback) => {
    axios
      .get(`/feeding/list?user=${userData.username}`).then(response => {
        console.log(response, 'response list feeding')
        callback(response.data)
      })
      .catch(() => {
        toast({
          component: ToastificationContent,
          props: {
            title: 'Erro ao carregar a lista',
            icon: 'AlertTriangleIcon',
            variant: 'danger',
          },
        })
      })
  }

  // *===============================================---*
  // *--------- UI ---------------------------------------*
  // *===============================================---*

  return {
    fetchInvoices,
    tableColumns,
    sortBy,
    refInvoiceListTable,
  }
}
